import { RendererCanvas } from '../../../../lib';

export class Solution extends RendererCanvas {

    edges = []
    nodes = []
    m = new Vector3(0,0,0)

    constructor(div: HTMLDivElement) {
        super(div);
        this.init();
    }
    public init() {
        // TODO

        const node = new Node(100, 100, 0);
        this.nodes.push(node)
        

        this.start()
    }
    public render(ctx: CanvasRenderingContext2D): void {

        this.edges.forEach((e: Edge)=> {
            e.render(ctx);
        }) 

        this.nodes.forEach((n: Node) => {
            n.render(ctx);
        })


        ctx.beginPath();
        ctx.arc(this.m.x, this.m.y, 2, 0, 3.14 * 2)
        ctx.closePath();
        ctx.fillStyle = '#888';
        ctx.fill();
    }
    public mouseClick(x: number, y: number): void {
        const node = new Node(x, y, 0);
        console.log(node, x, y);
        this.nodes.push(node);

        const len = this.nodes.length
        this.edges.push(new Edge(this.nodes[len-1], this.nodes[len-2]))
    }
    public mouseMove(x: number, y: number): void {
        this.m.x = x;
        this.m.y = y;
    }
}


class Edge {
    n0: Node
    n1: Node
    constructor(n0: Node, n1: Node) {
        this.n0 = n0;
        this.n1 = n1;
    }
    render(ctx: CanvasRenderingContext2D) {
        ctx.beginPath();
        ctx.moveTo(this.n0.vec.x, this.n0.vec.y)
        ctx.lineTo(this.n1.vec.x, this.n1.vec.y)
        ctx.strokeStyle = '#0f0';
        ctx.stroke();
    }
}
class Node {
    vec: Vector3;
    r: number = 5;
    constructor(x: number, y: number, z:number = 0) {
        this.vec = new Vector3(x, y, z)
    }
    render(ctx: CanvasRenderingContext2D) {
        ctx.beginPath();
        ctx.arc(this.vec.x, this.vec.y, this.r, 0, 3.14 * 2)
        ctx.closePath();
        ctx.fillStyle = '#ff0000';
        ctx.fill();
    }
}
class Vector3 {
    x: number;
    y: number;
    z: number;
    constructor(x: number, y: number, z:number = 0){
        this.x = x;
        this.y = y;
        this.z = z
    }
}